import "./assets/styles/style.scss"
import "./index.scss"

const list = document.querySelectorAll('li');
let iconClick;
let NumberClassList;
let onClickNumberClassList;

// Add EventListener on all icon exclamation circle by list element

list.forEach (li => {
  
  console.log(li.childNodes[5]);
  iconClick = li.childNodes[5];

  NumberClassList = li.classList.length;

  iconClick.addEventListener("click", (event) => {
  event.stopPropagation();
  
  onClickNumberClassList = li.classList.length;
  
  // Compare number of class on li element before and after click on icon

  if(onClickNumberClassList == NumberClassList) {
      addClass(li);
      } else {
      removeClass(li);
      }
    });
  });

// function add & remove class .important

const addClass = (li) => {
  li.classList.add('important');
  };

const removeClass = (li) => {
  li.classList.remove('important');
  };